.header-wrapper{
  margin: 0 auto;
  text-align: center;
}

.header-title{
    font: 700 25px Montserrat; 
    margin: 100px 0 100px 0;
    letter-spacing: 0;
}

.nav-wrapper{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;    
}
.credHeader{
  font: 400 17px Montserrat;
  color: dimgrey;
  margin-bottom: 20px;
}
.credBlock{
  width: 16rem;
  align-self: flex-end;
  text-align: center;
}
.react-datepicker-wrapper, 
.react-datepicker__input-container{
  width: 100%;
}
.search-btn{
    background: var(--mainGreen);
    color: white;
    border: 1px solid var(--mainGreen) !important;
}
.search-btn:hover{
    background: var(--darkGreen);
}

@media(max-width:835px){
  .header-title{
    margin: 20px 0;
  }
  .credBlock{
    width: 45% !important;
  }
  .nav-input{
      width: 100% !important;
      height: 40px;
  }
  .credHeader{
    display:none;
  }
}

@media(max-width:1024px){
  .credBlock{
    width: auto;
    margin-right: -1px;
  }
  .nav-input{
      width: 12rem;
  }
  .search-btn{
    border-left: 1px solid grey;
}
}