  .react-datepicker{
    border-radius: 0;
  }
  
  .react-datepicker__header{
    background-color: white;
    border-bottom: none;
    padding-top: 25px;
  }
  .react-datepicker__triangle{
    display:none;
  }

  .react-datepicker__day--selected{
    border-radius: 0;
    background-color: var(--mainGreen);
  }
  .react-datepicker__day, .react-datepicker__day-name{
    border-radius: 0;
    width: 2rem;
    line-height: 2rem;
  }
  .react-datepicker__navigation{
    top: 18px;
  }
  .react-datepicker__month {
    margin: 5px 1rem 1rem 1rem;
  }
  .react-datepicker__current-month{
    margin: -6px 0 10px 0;
  }
  .react-datepicker-popper[data-placement^="bottom"] {
    margin-top: 30px;
  }
.modal{
    display: block;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 0;
    visibility: hidden; 
  }

.timeBlock{
  max-width: 616px;
  margin: auto;
  margin-top: 40%;
}
.show-modal {
  opacity: 1;
  visibility: visible;
}
.hoursBtn, .hoursBtn2, .hoursDisabled{
  background: var(--mainGreen);
  border: none;
  margin: 4px;
  width: 80px;
  height: 40px;
  color: white;
  cursor: pointer;
  font: 500 13px Montserrat;
  &:hover{
    background: var(--darkGreen);
  }
}
.hoursDisabled{
  background: var(--notActive);
  color: #d0d0d0;
  cursor:default;
  pointer-events: none;
}

@media(max-width: 750px){
  .timeBlock{
    margin: 30px 5%;
  }
}
.amount-wrapper{
  position: relative;
}

.amount-input{
  cursor: default;
  text-align: center;
  padding: 0;
  padding-bottom: 2px;
}

.add-num, .sub-num{
  position: absolute;
  background: none;
  border: none;
  font-size: 20px;
  cursor: pointer;
}
.add-num{
  right: 30px;
  bottom: 6px;
}
.sub-num{
  left: 35px;
  bottom: 7px;
}

@media(max-width:835px){
  .add-num{
    right: 30px;
    bottom: 6px;
  }
  .sub-num{
    left: 45px;
    bottom: 7px;
  }
}

@media(max-width:1024px){
  .add-num{
    right: 11px;
  }
  .sub-num{
    left: 12px;
  }
}
.header-wrapper{
  margin: 0 auto;
  text-align: center;
}

.header-title{
    font: 700 25px Montserrat; 
    margin: 100px 0 100px 0;
    letter-spacing: 0;
}

.nav-wrapper{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;    
}
.credHeader{
  font: 400 17px Montserrat;
  color: dimgrey;
  margin-bottom: 20px;
}
.credBlock{
  width: 16rem;
  -webkit-align-self: flex-end;
          align-self: flex-end;
  text-align: center;
}
.react-datepicker-wrapper, 
.react-datepicker__input-container{
  width: 100%;
}
.search-btn{
    background: var(--mainGreen);
    color: white;
    border: 1px solid var(--mainGreen) !important;
}
.search-btn:hover{
    background: var(--darkGreen);
}

@media(max-width:835px){
  .header-title{
    margin: 20px 0;
  }
  .credBlock{
    width: 45% !important;
  }
  .nav-input{
      width: 100% !important;
      height: 40px;
  }
  .credHeader{
    display:none;
  }
}

@media(max-width:1024px){
  .credBlock{
    width: auto;
    margin-right: -1px;
  }
  .nav-input{
      width: 12rem;
  }
  .search-btn{
    border-left: 1px solid grey;
}
}
body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
      "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
  
code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
:root{
    --mainGreen: rgb(103, 175, 103);
    --darkGreen: #488d36;
    --notActive: #efefef;
    --tableHover: red;
}
textarea:focus, input:focus, button:focus{
    outline: none;
}
input[type=text] {   
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border-radius: 0;
}
.nav-input{
    width: 13rem;
    height: 40px;
    padding-left: 15px;
    font: inherit;
    padding-bottom: 2px;
    margin-top: 15px;
    border: 1px solid gray;
    box-sizing: border-box;
    cursor: pointer;
}

