.amount-wrapper{
  position: relative;
}

.amount-input{
  cursor: default;
  text-align: center;
  padding: 0;
  padding-bottom: 2px;
}

.add-num, .sub-num{
  position: absolute;
  background: none;
  border: none;
  font-size: 20px;
  cursor: pointer;
}
.add-num{
  right: 30px;
  bottom: 6px;
}
.sub-num{
  left: 35px;
  bottom: 7px;
}

@media(max-width:835px){
  .add-num{
    right: 30px;
    bottom: 6px;
  }
  .sub-num{
    left: 45px;
    bottom: 7px;
  }
}

@media(max-width:1024px){
  .add-num{
    right: 11px;
  }
  .sub-num{
    left: 12px;
  }
}