.modal{
    display: block;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 0;
    visibility: hidden; 
  }

.timeBlock{
  max-width: 616px;
  margin: auto;
  margin-top: 40%;
}
.show-modal {
  opacity: 1;
  visibility: visible;
}
.hoursBtn, .hoursBtn2, .hoursDisabled{
  background: var(--mainGreen);
  border: none;
  margin: 4px;
  width: 80px;
  height: 40px;
  color: white;
  cursor: pointer;
  font: 500 13px Montserrat;
  &:hover{
    background: var(--darkGreen);
  }
}
.hoursDisabled{
  background: var(--notActive);
  color: #d0d0d0;
  cursor:default;
  pointer-events: none;
}

@media(max-width: 750px){
  .timeBlock{
    margin: 30px 5%;
  }
}